.Nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    position: sticky;
    top: 0;
    background-color: #282c34;
    padding-block-start: 1vh;
}

.nav-icon {
    height: 5vh;
    cursor: pointer;
}

.nav-title {
    margin-inline: auto;
}